.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color:black;
    opacity: 0.8;
}
.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50% -50%);
  top: 30%;
  left: 30%;
  text-align: center;
}
.hero .content h1{
    font-size: 4rem;
    padding: 0.4rem 0 1.2rem;

}
.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media screen and (max-width:640px) 
{
    .hero .content h1{
        font-size: 1rem;
        text-align: center;
        justify-content: center;
     
    
    }
    .hero .content p{
        font-size:1rem;
        text-align: center;
        justify-content: center;
     
    }
    .into-img{
       height: 10vh;
    }
    .btn{
        padding: 2px  2px;
        font-size: 1rem;
        text-transform: uppercase;
        background: yellow;
        color: black;
        border: 1px solid yellow;
        font-weight: 400;
        cursor: pointer;
      
      }
      .btn-light{
        background: transparent;
        padding: 2px 2px;
        border: 1px solid yellow;
        color: #fff;
        cursor: pointer;
      }
    .content .btn,.content .btn-light{   
        display: flex;
        flex-direction: column;
}
}