
.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.hero2-img {
     content: "";
    background-image: url(https://images.unsplash.com/photo-1605379399642-870262d3d051?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=906&q=80);
     background-size: cover;
     background-position: center;
     width: 100%;
    height: 90vh;
     position:relative;
     top: 0;
     left: 0;
     z-index: -1;
}
.hero2-img h1{
    font-size: 2.4rem;
}
.hero2-img p {
    font-size: 1.4rem;
    text-align: center;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@media screen and (max-width:640px) {
    .hero2-img h1{
        font-size: 2.5rem;
    }
    .hero2-img p {
        font-size: 1.4rem;
        text-align: center;
    }
    .hero2-img{
        width: 100%;
        height: 50vh;  
    }
}