.work-container{
  background-color: bisque;
}
.project-heading{
    text-align: center;
    padding: 4rem 0 3rem 0;
    color: black;
}
.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:40px;
    padding-bottom: 5rem;

}
.project-card:hover{
    background-color:red;
}
.project-card{
    background: gray;
    padding: 1.2rem 1rem;
}
.project-card img{
    width: 100%;
}
.project-title{
    color:black;
    padding: 1rem 0;
}
.pro-detail p{
    padding-bottom: 1rem;
    text-align: justify;
    font-size: 1.1rem;
}
.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

@media screen and (max-width:740px) {
    .project-container{
        max-width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns:1fr;
          grid-gap: 40px;
    }
    
}