.pricing{
    width: 100%;
    padding: 6rem 2rem ;
    background-color:gray;
}
.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,3fr);
      grid-gap: 40px;
}
.card{
    border: 1px solid wheat;
    color: wheat;
    text-align: center;
    padding: 1rem;
}
.card:hover{
    background-color:gold;
}
.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}
.btc{
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
}

.bar{
    border: 1px solid wheat;
    width: 20%;
    margin: 1.5rem auto;
    display: block;
}
.card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}
.card p{
    padding: 10px 0;
}
@media screen and (max-width:740px) {
    .card-container{
        max-width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns:1fr;
          grid-gap: 40px;
    }
    
}